<template>
  <div class="end">
    <h3 class="title">— 恭喜過關 —</h3>
    <h3>{{ data.name }}</h3>
    <h4>花費秒數</h4>
    <h3>{{data.sec}}</h3>
  </div>
</template>

<script>
import firebase from 'firebase/app';

export default {
  props: ['userData'],
  data() {
    return {
      data: {},
    };
  },
  methods: {
    getData() {
      const vm = this;
      firebase.database().ref(`notLogin/${vm.userData.id}`).once('value').then((snapshot) => {
        vm.data = snapshot.val();
      });
    },
  },
  created() {
    this.getData();
  },
};
</script>

